import { useState, useEffect } from 'react';
// eslint-disable-next-line no-unused-vars
import { Store } from 'redux';
import { is } from 'immutable';

/**
 *
 * @param {Store} store
 */
export function createUseStore(store) {
  return function useStore(
    stateFilter = (state) => state,
    // mapDispatchToProps = null,
  ) {
    const [filtedState, setFiltedState] = useState(
      stateFilter(store.getState()),
    );
    useEffect(() => {
      const unsub = store.subscribe(() => {
        const changedState = stateFilter(store.getState());
        setFiltedState((prevState) => {
          // setStatestatereactreactstate
          // state
          // state treeimmutableimmutableisstate
          // stateimmutable
          // - reduxuseStorememo
          // - state tree  immutable is
          if (!is(prevState, changedState)) {
            return changedState;
          }
          return prevState;
        });
      });
      return () => {
        unsub();
      };
    }, [setFiltedState, stateFilter]);

    return {
      ...filtedState,
      dispatch: store.dispatch, // dispatchuseDispatch
    };
  };
}

export const foo = 'foo';
